<template>
  <div class="wrap_infor_chart">
    <div class="head">
      <h2>AMChart,Axios Async 테스트</h2>
      <div class="choice_box">
        <v-autocomplete
          single-line
          ref="first"
          class="w120"
          :items="coinList"
          item-text="name"
          item-value="name"
          label="XRP"
          solo
          v-model="sendDataKr.fsym.name"
          @change="change"
        ></v-autocomplete>
        <v-select
          class="ml-5 w120"
          :items="duration"
          solo
          v-model="sendDataKr.duration"
          item-text="text"
          item-value="val"
          ldjjdfldjdlljdjfds
          @change="change"
          single-line
        ></v-select>
      </div>
      <transition name="fade">
        <div class="warp_loading" v-if="isLoading">
          <div class="loding">
            <!-- <v-progress-circular
              :size="50"
              color="primary"
              indeterminate
            ></v-progress-circular> -->
            <lottie-animation
              ref="find"
              :loop="true"
              :autoPlay="true"
              :speed="3"
              :animationData="require('@/assets/money.json')"
            />
          </div>
        </div>
        <div v-else-if="error">Error!</div>
      </transition>
    </div>

    <div class="chart_00" ref="chartdiv"></div>
    <template
      ><v-data-table
        :hide-default-header="true"
        :headers="priceHeader"
        :items="priceData"
        :items-per-page="10"
        :disable-sort="false"
        class="mt-5 table_01"
      >
        <template v-slot:header="{ props: { headers } }">
          <thead class="">
            <tr>
              <th
                v-for="(item, index) in headers"
                :class="`idx_${index}`"
                :key="item.text"
                class=""
              >
                {{ item.text }}
              </th>
            </tr>
          </thead>
        </template>
        <template v-slot:body="{ items }">
          <tbody>
            <tr
              v-for="(item, index) in items"
              :key="item.name"
              :class="[trColor(item.change24Hour)]"
            >
              <td
                :class="`idx_${index}`"
                class=""
              >
                {{ item.index }}
              </td>
              <td class="text-center">
                <div class="d-flex space-between align-center justify-center">
                  <img :src="item.img" alt="" /><span class="name">{{
                    item.name
                  }}</span>
                </div>
              </td>
              <td class="text-end color_effect">
                {{ item.krw }} ₩
              </td>
              <td class="text-end">
                {{ item.primium }}%
              </td>
              <td class="text-end color_effect">
                {{ item.usd }} $
              </td>
              <td class="text-end color_effect">
                {{ item.change24Hour }} ₩
              </td>
            </tr>
          </tbody>
        </template>
      </v-data-table></template
    >
  </div>
</template>


<script>
import axios from "axios";
import LottieAnimation from "lottie-web-vue";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4lang_ko_KR from "@amcharts/amcharts4/lang/ko_KR";
am4core.useTheme(am4themes_animated);

export default {
  components: {
    LottieAnimation,
  },
  data() {
    return {
      exChangeRate: [],
      coinList: [{ name: "XRP" }],
      duration: [
        { text: "일간 그래프", val: "histoday" },
        { text: "시간 그래프", val: "histohour" },
        { text: "분당 그래프", val: "histominute" },
      ],

      isLoading: true,
      error: null,
      sendDataKr: {
        duration: "histominute",
        fsym: { name: "XRP" },
        tsym: "KRW",
        limit: "50",
        // Apikey:
        //   "bd1c741f37bd77cd9f5554daed59741b3a75101cf1b5437be864fb4e4bafb0d6",
      },
      chartData: [],
      priceHeader: [
        { text: "인덱스", value: "idx", align: "center" },
        { text: "코인명", value: "name", align: "center" },
        { text: "한국 ₩", value: "krw", align: "end", width: "150px" },
        { text: "코프 %", value: "primium", align: "end", width: "150px" },
        { text: "미국 $", value: "usd", align: "end", width: "150px" },
        {
          text: "하루동안 변동  ₩",
          value: "CHANGEDAY",
          align: "end",
          width: "150px",
        },
      ],
      priceData: [
        [
          {
            img:
              "https://www.cryptocompare.com/media/37746347/xrp.png?width=25",
            name: "XRP",
            krw: 1862.79,
            usd: 1.513,
            change24Hour: "9.7",
            primium: "8.1",
          },
          {
            img:
              "https://www.cryptocompare.com/media/37746235/ada.png?width=25",
            name: "ADA",
            krw: 2531.01,
            usd: 2.06,
            change24Hour: "6.7",
            primium: "7.9",
          },
          {
            img:
              "https://www.cryptocompare.com/media/37746339/doge.png?width=25",
            name: "DOGE",
            krw: 586.0688435339999,
            usd: 0.4917,
            change24Hour: "-4.5",
            primium: "5.1",
          },
          {
            img:
              "https://www.cryptocompare.com/media/37746880/bnb.png?width=25",
            name: "BNB",
            krw: 643850.2788120001,
            usd: 523.11,
            change24Hour: "8504.4",
            primium: "8.1",
          },
          {
            img:
              "https://www.cryptocompare.com/media/37746243/ltc.png?width=25",
            name: "LTC",
            krw: 372873.68,
            usd: 303.1,
            change24Hour: "23597.4",
            primium: "8.0",
          },
        ],
      ],
    };
  },

  methods: {
    // exchange
    trColor(inputVal) {
      let trCalssVal = inputVal > 1 || inputVal == 1 ? "up" : "down";
      return trCalssVal;
    },
    //차트 업데이트
    change() {
      (this.isLoading = true), this.chart1();
    },
    // 환률정보 가져오기
    getExChangeMoney() {
      let exChangeData = [
        {
          update: 1621469502161,
          USDKRW: [1131.75],
          JPYKRW: [10.343],
          CNYKRW: [176.007],
        },
      ];
      this.exChangeRate = exChangeData;
      console.log("exChangeRate", this.exChangeRate);
    },

    // 코인리스트
    async getCoinList() {
      try {
        const responseColinList = await axios.get(
          `https://min-api.cryptocompare.com/data/top/totaltoptiervolfull`,
          {
            params: {
              // 요청제한
              // api_key: this.sendDataKr.Apikey,
              limit: 69,
              tsym: "KRW",
            },
          }
        );
        const coinList = [];
        for (let i = 0; i < responseColinList.data.Data.length; i++) {
          coinList.push({
            name: responseColinList.data.Data[i].CoinInfo.Name,
            img: responseColinList.data.Data[i].CoinInfo.ImageUrl,
          });
        }
        // 클레이 추가
        coinList.unshift({
          name: "KLAY",
          img: "/media/36569485/klay.png",
        });
        this.coinList = coinList;
        console.log("this.coinList after", this.coinList);
      } catch (error) {
        console.error(error);
      }
    },

    // 테이블 데이터
    async getTableData() {
      try {
        let pramsFsms = this.coinList.map((e) => e.name).join(",");
        console.log("pramsFsms", pramsFsms);

        const responsePriceTable = await axios.get(
          `https://min-api.cryptocompare.com/data/pricemultifull`,
          {
            params: {
              Apikey: this.sendDataKr.Apikey,
              fsyms: pramsFsms,
              tsyms: "krw,USD",
            },
          }
        );
        // console.log('responsePriceTable',responsePriceTable)
        const tableData = [];
        for (
          let i = 0;
          i < Object.keys(responsePriceTable.data.RAW).length;
          i++
        ) {
          tableData.push({
            index: i,
            img:
              "https://www.cryptocompare.com" +
              this.coinList[i].img +
              "?width=25",
            name: Object.keys(responsePriceTable.data.RAW)[i],
            krw: responsePriceTable.data.RAW[
              Object.keys(responsePriceTable.data.RAW)[i]
            ].KRW.PRICE.toFixed(1),

            usd: responsePriceTable.data.RAW[
              Object.keys(responsePriceTable.data.RAW)[i]
            ].USD.PRICE.toFixed(1),
            change24Hour: responsePriceTable.data.RAW[
              Object.keys(responsePriceTable.data.RAW)[i]
            ].KRW.CHANGEDAY.toFixed(1),
            // usdKrw: (
            //   responsePriceTable.data.RAW[Object.keys(responsePriceTable.data.RAW)[i]]
            //     .USD * this.exChangeRate[0].USDKRW
            // ).toFixed(1),
            primium: (
              ((responsePriceTable.data.RAW[
                Object.keys(responsePriceTable.data.RAW)[i]
              ].KRW.PRICE -
                responsePriceTable.data.RAW[
                  Object.keys(responsePriceTable.data.RAW)[i]
                ].USD.PRICE *
                  this.exChangeRate[0].USDKRW) /
                responsePriceTable.data.RAW[
                  Object.keys(responsePriceTable.data.RAW)[i]
                ].KRW.PRICE) *
              100
            ).toFixed(1),
          });
        }

        this.priceData = tableData;
        console.log("priceData", tableData);
      } catch (error) {
        console.log(Object.keys(error), error.message);
      }
    },

    async chart1() {
      try {
        let duration = this.sendDataKr.duration;
        const responseChart = await axios.get(
          `https://min-api.cryptocompare.com/data/v2/${duration}`,
          {
            params: {
              fsym: this.sendDataKr.fsym.name,
              Apikey: this.sendDataKr.Apikey,
              limit: this.sendDataKr.limit,
              tsym: this.sendDataKr.tsym,
            },
          }
        );

        const getChartData = [];
        for (let i = 0; i < responseChart.data.Data.Data.length; i++) {
          getChartData.push({
            high: responseChart.data.Data.Data[i].high,
            low: responseChart.data.Data.Data[i].low,
            open: responseChart.data.Data.Data[i].open,
            close: responseChart.data.Data.Data[i].close,
            date: new Date(responseChart.data.Data.Data[i].time * 1000),
            volume: responseChart.data.Data.Data[i].volumeto,
            value: responseChart.data.Data.Data[i].open,
          });
        }
        this.chartData = getChartData;

        this.drawChart();
        this.isLoading = false;
      } catch (error) {
        console.log(Object.keys(error), error.message);
      }
    },
    drawChart() {
      let chart = am4core.create(this.$refs.chartdiv, am4charts.XYChart);

      chart.padding(0, 15, 0, 15);
      chart.language.locale = am4lang_ko_KR;
      chart.data = this.chartData;
      chart.dateFormatter.inputDateFormat = "yyyy-MM-dd";
      chart.leftAxesContainer.layout = "vertical";
      let dateAxis = chart.xAxes.push(new am4charts.DateAxis());
      dateAxis.renderer.grid.template.location = 0;
      dateAxis.renderer.ticks.template.length = 8;
      dateAxis.renderer.ticks.template.strokeOpacity = 0.1;
      dateAxis.renderer.grid.template.disabled = true;
      dateAxis.renderer.ticks.template.disabled = false;
      dateAxis.renderer.ticks.template.strokeOpacity = 0.2;
      dateAxis.renderer.minLabelPosition = 0.01;
      dateAxis.renderer.maxLabelPosition = 0.99;
      dateAxis.keepSelection = true;
      dateAxis.minHeight = 30;

      dateAxis.groupData = true;
      dateAxis.minZoomCount = 5;

      let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
      valueAxis.tooltip.disabled = false;
      valueAxis.marginTop = 20;
      valueAxis.zIndex = 1;
      valueAxis.renderer.baseGrid.disabled = true;
      // height of axis
      valueAxis.height = am4core.percent(65);

      valueAxis.renderer.gridContainer.background.fill = am4core.color("#fff");
      valueAxis.renderer.gridContainer.background.fillOpacity = 0.6;
      valueAxis.renderer.inside = true;
      valueAxis.renderer.labels.template.verticalCenter = "bottom";
      valueAxis.renderer.labels.template.padding(2, 2, 2, 2);

      valueAxis.renderer.fontSize = "12px";
      let series = chart.series.push(new am4charts.CandlestickSeries());
      series.dataFields.dateX = "date";
      series.dataFields.valueY = "close";
      series.dataFields.openValueY = "open";
      series.dataFields.lowValueY = "low";
      series.dataFields.highValueY = "high";
      series.dropFromOpenState.properties.fill = am4core.color("#ff5252");
      series.dropFromOpenState.properties.opacity = 0.8;
      series.dropFromOpenState.properties.stroke = am4core.color("#ff5252");
      series.riseFromOpenState.properties.fill = am4core.color("#2196f3");
      series.riseFromOpenState.properties.opacity = 0.8;
      series.riseFromOpenState.properties.stroke = am4core.color("#2196f3");
      series.tooltipText =
        "시가:₩{openValueY.value}\n저가:₩{lowValueY.value}\n고가:₩{highValueY.value}\n종가:₩{valueY.value}";
      // series.tooltip.getFillFromObject = false;
      series.defaultState.transitionDuration = 1000;
      series.hiddenState.transitionDuration = 1000;
      series.tooltip.animationDuration = 500;
      series.tooltip.animationDuration = 500;
      chart.cursor = new am4charts.XYCursor();

      let valueAxis2 = chart.yAxes.push(new am4charts.ValueAxis());
      valueAxis2.tooltip.disabled = true;
      // height of axis
      valueAxis2.height = am4core.percent(35);
      valueAxis2.zIndex = 3;
      // this makes gap between panels
      valueAxis2.marginTop = 30;
      valueAxis2.renderer.baseGrid.disabled = true;
      valueAxis2.renderer.inside = true;
      valueAxis2.renderer.labels.template.verticalCenter = "bottom";
      valueAxis2.renderer.labels.template.padding(2, 2, 2, 2);
      //valueAxis.renderer.maxLabelPosition = 0.95;
      valueAxis2.renderer.fontSize = "12px";

      valueAxis2.renderer.gridContainer.background.fill = am4core.color("#fff");
      valueAxis2.renderer.gridContainer.background.fillOpacity = 0.6;

      let series2 = chart.series.push(new am4charts.ColumnSeries());
      series2.dataFields.dateX = "date";
      series2.clustered = false;
      series2.dataFields.valueY = "volume";
      series2.yAxis = valueAxis2;
      series2.tooltipText = "{valueY.value}";
      series2.name = "Series 2";

      // volume should be summed
      series2.groupFields.valueY = "sum";
      series2.defaultState.transitionDuration = 0;
      // a separate series for scrollbar
      let lineSeries = chart.series.push(new am4charts.LineSeries());
      lineSeries.dataFields.dateX = "date";
      lineSeries.dataFields.valueY = "close";
      // need to set on default state, as initially series is "show"
      lineSeries.defaultState.properties.visible = false;

      // hide from legend too (in case there is one)
      lineSeries.hiddenInLegend = true;
      lineSeries.fillOpacity = 0.5;
      lineSeries.strokeOpacity = 0.5;

      let scrollbarX = new am4charts.XYChartScrollbar();
      scrollbarX.series.push(lineSeries);
      chart.scrollbarX = scrollbarX;

      this.chart = chart;
    },
  },

  beforeDestroy() {
    if (this.chart) {
      this.chart.dispose();
    }
  },
  mounted() {},
  async created() {
    this.chart1();
    await this.getExChangeMoney();
    this.$refs.first.focus();
    await this.getCoinList();
    await this.getTableData();
    this.isLoading = false;
  },
  computed: {
    isMobile() {
      return this.$vuetify.breakpoint.name === "xs";
    },
  },
};
</script>

<style lang="css">
</style>